* {
  margin: 0;
  padding: 0; }

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px; }

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white; }

.navList {
  display: -ms-flexbox;
  display: flex;
  background: #eee; }
  .navList li {
    -ms-flex: 1 1;
        flex: 1 1;
    height: 32px;
    line-height: 32px; }

.footerBar {
  display: -ms-flexbox;
  display: flex;
  background: #eee; }
  .footerBar li {
    -ms-flex: 1 1;
        flex: 1 1;
    height: 32px;
    line-height: 32px; }

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif; }

ul, li {
  list-style-type: none; }



